.main{min-height:100vh}.contact{padding:4rem 0;background-color:#f5f5f5}.container{max-width:1200px;margin:0 auto;padding:0 1rem}.title{text-align:center;font-size:2.5rem;margin-bottom:3rem;color:#1a1a1a}.content{display:grid;grid-template-columns:1fr 1fr;gap:4rem}.info .infoTitle{font-size:1.8rem;margin-bottom:2rem;color:#1a1a1a}.details{display:grid;gap:2rem}.item .itemTitle{font-size:1.2rem;color:#0070f3;margin-bottom:.5rem}.item .itemText{color:#666;line-height:1.6}.form{background:#fff;padding:2rem;border-radius:10px;box-shadow:0 2px 4px rgba(0,0,0,.1)}.field{margin-bottom:1.5rem}.field .label{display:block;margin-bottom:.5rem;color:#1a1a1a;font-weight:500}.field .input,.field .textarea{width:100%;padding:.8rem;border:1px solid #ddd;border-radius:5px;font-size:1rem}.field .input:focus,.field .textarea:focus{outline:none;border-color:#0070f3}.field .textarea{min-height:150px;resize:vertical}.button{background-color:#0070f3;color:#fff;border:none;padding:1rem 2rem;font-size:1.1rem;border-radius:5px;cursor:pointer;width:100%;transition:background-color .3s ease}.button:hover{background-color:#0051a2}